*{
  margin: 0%;
  padding: 0%;
  font-family: "Roboto", sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@300&family=Montserrat:wght@900&family=Roboto:wght@100;300;400&family=Ubuntu:wght@300&display=swap');

nav{
      background-color: rgb(7, 10, 158);
      color: white;
      padding:20px;
      text-align: center;
}

.container{
  background-color:rgb(254,244,221);
  width: 70%;
  min-height: 100vh;
  margin: auto;
  padding: 4vmax 8vmax;
  box-sizing: border-box;
}

.container>form{
  display: flex;
  flex-direction:column;
}

.container>h1{
  text-align: center;
  margin: 30px;
  font-size: 2.5rem;
}

input,textarea,button{
  padding: 15px;
  border: 1px solid black;
  border-radius:5px;
  margin: 10px 0;
  font-size: 1.2em;
}

button{
  border: none;
  cursor: pointer;
  background-color:rgb(54, 52, 53);
  color: white;
}

.task{
  display: flex;
  background-color: white;
  padding:20px;
  border-radius: 5px;
  margin: 20px 0;
  align-items: center;
  
}

.task span{
  color: rgb(78, 74, 74);
}

.task > div{
  width: 100%;

}

.task button{
  border-radius:100%;
  width: 30px;
  height: 30px;
  font-size: 1.6rem;
  display: flex;
  justify-content:center;
  align-items:center;
  background-color: crimson;
}

.task button:hover{
  background-color: red;
}